*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    /* -webkit-user-select: none;
    -webkit-user-drag: none;
    -webkit-user-region: none; */
}

body{
    background-color: burlywood;
    display: grid;
    place-content: center;
    font-family: 'Roboto', sans-serif;
    height: 100vh;
    font-size: 2rem;
}
.container{
    height: 80vh;
    width: 50vh;
    background-color: black;
    border-radius: 1rem;
    padding: 1rem;
    box-shadow: 7px 7px 5px 0px rgba(50, 50, 50, 0.75);
}
.wrapper{
    display: grid;
    height: 100%;
    width: 100%;
    grid-template-columns: repeat(4,1fr);
    grid-template-rows: (6,1fr);
    grid-gap: 10px;
    gap: 10px;
}

.header{
    font-size: 3rem;
    text-align: center;
    font-weight: bold;
}

.screen{
    grid-column: 1 / -1;
    color: white;
    text-align: right;
    padding: 0.5rem;
    font-size: 4rem;
    overflow: hidden;
}

.btn{
    display: grid;
    place-content: center;
    background-color: rgb(54, 52, 52);
    border-radius: 50%;
    color: white;
    cursor: pointer;
    font-weight: bold;
}

.btn:hover{
    background-color: grey;
}

.light-grey{
    background-color: gray;
    color: black;
}

.light-grey:hover{
    background-color: white;
}

.orange{
    background-color: orange;
}
.orange:hover{
    background-color: white;
    color: orange;
}

.zero{
    grid-column: 1 / span 2;
    border-radius: 25px;
    place-content: unset;
    align-items: center;
    padding-left: 2rem;
}
